<template>
  <div>
    <div class="container login">
      <form class="form-horizontal  col-sm-5 col-sm-offset-3 form_box" name="myform" >
        <div class="form-group col-sm-12">
          <label for="ID" class="control-label col-sm-2">帐号</label>
          <div class=" input-group">
            <input type="text" v-model="user.id" class="form-control" id="ID" required name="id" placeholder="请输入帐号">
            <div class="input-group-addon">
              <span class="glyphicon glyphicon-user "></span>
            </div>
          </div>
        </div>

        <div class="form-group col-sm-12">
          <label for="pass" class="control-label col-sm-2">密码</label>
          <div class="input-group">
            <input type="password" v-model="user.pass" class="form-control" id="pass" required name="pass" placeholder="请输入密码">
            <div class="input-group-addon">
              <span class="glyphicon glyphicon-lock "></span>
            </div>
          </div>
        </div>
        <div class="form-group col-sm-12">
            <label for="loginCode" class="control-label col-sm-2">验证码</label>
            <div class="col-sm-3 logincode ">
              <div class="input-group col-sm-12">
              <input type="text" v-model="user.code" class="form-control" id="loginCode" required name="loginCode" placeholder="输入验证码">
            </div>
            </div>
            <div class="col-sm-7 logincode ">
               <img :src="imgsrc" class="col-sm-6 logincode codeimg" ref="img" name="img">
               <a class="btn btn-link col-sm-6" @click="clickIMG">看不清，换一张</a>
            </div>
        </div>
        <div class="form-group">
          <div class="col-sm-3 col-sm-offset-4">
            <button type="button" class="btn btn-info btn-block" href="javascript:void(0)" @click="loginByTel">登录</button>
          </div>
        </div>
        <a href="#/rigster" class="btn btn-link pull-right">注册新用户</a>
      </form>
    </div>
  </div>
</template>
<script>
import '@/assets/css/index.css'
export default {
  data: function() {
    return {
      user: {
        id: "",
        pass: "",
        code: ""
      },
      imgsrc: this.HOST+"captcha.jpg",
    };
  },
  methods: {
    //换验证码图片
    clickIMG() {
      this.$refs.img.src =this.imgsrc + "?" + new Date();
    },
    loginByTel() {
      var vm = this;
      var user = vm.user;
      if (user.id != "" && user.pass != "") {
        vm.$axios
          .post(vm.HOST+"login.action", user)
          .then(function(response) {
            vm.opreateResp(response.data);
          })
          .catch(function(error) {
            console.log(error);
            vm.$layer.msg('网络错误，请检查网络是否畅通',{icon:3});
          });
      }
    },
    opreateResp(data){
      var vm=this;
      switch (data.code) {
              case "0":
                vm.$store.commit("UPDATE_USER", {id:data.id});
                vm.$store.commit("SET_IS_LOGIN", true);
                vm.$layer.msg('登录成功',{icon:1,time:1000,end:function(index, layeros){
                  vm.$router.push("/main");
                }});
                break;
              case "500":
              vm.$layer.msg(data.msg,{icon:2,time:1000,});
                vm.clickIMG();
                vm.user.pass=vm.user.code="";
                break;
            }
            return ;
    }
  }
};
</script>
<style scoped>
.control-label {
  text-align: left !important;
  padding-right: 0;
}

.logincode {
  padding: 0 !important;
}
.codeimg {
  height: 33px;
  float: left;
  padding-left: 1rem !important;
}
</style>



